        <p><span>ÊùÉÂøóÈæôÔºö</span>ÎÇòÎäî ÏÑúÎ™ÖÔΩû</p>
        <p><span>ÁéãÊÄùËÅ™Ôºö</span>ÂéªÂì™Áé©ÂïäÔºüÈÇ£‰πàÁàΩ</p>
        <p>
          <span class="data-name">‰∏áËôéÁßëÊäÄ~Ë¥æÁ¥†Êù∞</span> ÂõûÂ§ç
          <span>ÁéãÊÄùËÅ™</span>
          <span>Ôºö</span> Êæ≥Ê¥≤Â§ßÂ†°Á§ÅÔºåËøôËæπÂàöÂ•ΩÊòØÂ§èÂ≠£ÔºåÊå∫ÈÄÇÂêàÈÅøÂØíÁöÑ„ÄÇ
        </p>
        <p><span>Êù®ÂπÇÔºö</span>üòòÁßÅ‰∫∫È£ûÊú∫Âá∫Ë°åÔºåÊ±ÇÂ∏¶‰∏äÊàëÔΩû</p>
      </div>
    </div>
  </div>
</div>
</template>


<script>
export default {
  props: ['childlist'],
  name: 'CircleListItem',
  data() {
    return {
      list: [],
      visible: false
    }
  },
  methods: {
    togglePanel() {
      this.visible ? this.hide() : this.show()
    },
    show() {
      this.visible = true
      document.addEventListener('click', this.hidePanel, false)
    },

    hide() {
      this.visible = false
      document.removeEventListener('click', this.hidePanel, false)
    },
    hidePanel(e) {
      if (!this.$refs.commentsmain.contains(e.target)) {
        this.hide()
      }
    },
    /**
     * ÁÇπËµû
     */
    good() {
      this.$dialog.toast({
        mes: 'ÁÇπËµû!',
        timeout: 1500
      });
      this.visible = false;
    },
    /**
     * ËØÑËÆ∫
     */
    comment() {
      this.visible = false;
      this.$emit('showinput');
    }

  },
  beforeDestroy() {
    this.hide()
  }
}
</script>

<style>
#list,
