}

.focus-btn {
  border: 1px solid #BB6804;
  border-radius: 3px;
  padding: 3px 5px;
  font-size: 10px;
  position: absolute;
  right: 0;
  color: #BB6804;
}

.comments {
  position: absolute;
  background: #666;
  padding: 5px;
  right: 25px;
  border-radius: 3px;
  bottom: 0px;
}

.comments .goodimg {
  width: 20px;
  height: 20px;
  padding: 3px;
  margin: 0 15px;
  float: left;
}

.comments .commentsimg {
  width: 20px;
  height: 20px;
  padding: 3px;
  margin: 0 15px;
  float: left;
}
</style>



